import styles from "./Button.module.scss";
import Button from "@mui/material/Button";

export const CustomButton = ({
  variant,
  text,
  color,
  onClick,
  startIcon,
  centerIcon,
  endIcon,
  className,
}: {
  variant?: "text" | "outlined" | "contained";
  text?: string;
  color?:
    | "inherit"
    | "primary"
    | "secondary"
    | "success"
    | "error"
    | "info"
    | "warning"
    | "neutral"
    | "red";
  onClick?: () => void;
  startIcon?: React.ReactNode;
  centerIcon?: React.ReactNode;
  endIcon?: React.ReactNode;
  className?: string;
  disabled?: boolean;
}) => {
  return (
    <Button
      color={color}
      disableElevation
      onClick={onClick}
      variant={variant}
      startIcon={startIcon}
      endIcon={endIcon}
      classes={{
        root: `${styles.btnCustomRoot} ${className}`,
        outlined: styles.outlined,
        contained: styles.contained,
      }}
      className={className}
    >
      {text} {centerIcon}
    </Button>
  );
};
