import React from "react";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import "@splidejs/react-splide/css/skyblue";
import styles from "./Slideshow.module.scss";
import Spritesheet from "react-responsive-spritesheet";
import heroPic1 from "../../../images/wallpapers/HeroPic1.png";
import heroPic2 from "../../../images/wallpapers/HeroPic2.png";
import heroPic3 from "../../../images/wallpapers/HeroPic3.png";
import heroPic1Sprite from "../../../images/wallpapers/HeroPic1-Sprite.png";
import heroPic2Sprite from "../../../images/wallpapers/HeroPic2-Sprite.png";
import heroPic3Sprite from "../../../images/wallpapers/HeroPic3-Sprite.png";

export interface SlideshowProps {
  // SlidesProperties?: (string)[]
  Size?: number
}

export const Slideshow: React.FC<SlideshowProps> = ({Size}) => {
  const Slides = [
    { image: heroPic1, sprite: heroPic1Sprite },
    { image: heroPic2, sprite: heroPic2Sprite },
    { image: heroPic3, sprite: heroPic3Sprite },
  ];

  const HeroSlideImages = (image: string, sprite: string) => {
    return (
      <SplideSlide>
        <Spritesheet
          className={styles.sprite}
          direction="forward"
          image={sprite}
          widthFrame={739}
          heightFrame={716}
          steps={49}
          fps={30}
          autoplay={true}
          loop={true}
          background={image}
          backgroundSize={`cover`}
          backgroundRepeat={`no-repeat`}
          backgroundPosition={`center center`}
        />
      </SplideSlide>
    );
  };

  return (
    <Splide
      aria-label="My Favorite Images"
      options={{
        rewind: true,
        height: "716px",
        width: "739px",
        type: "fade",
        autoplay: true,
        pauseOnHover: false,
        resetProgress: false,
        arrows: false,
        pagination: false,
        interval: 6000,
      }}
    >
      {Slides.map((slideItems) =>
        HeroSlideImages(slideItems.image, slideItems.sprite)
      )}
    </Splide>
  );
};
