import React from "react";
import ReactDOM from "react-dom/client";
import { App } from "./routes/App";
import { CustomThemeProvider } from "./theme";

const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);
root.render(
  <React.StrictMode>
    <CustomThemeProvider>
      <App />
    </CustomThemeProvider>
  </React.StrictMode>
);
