import styles from './ListAddRemove.module.scss';
import React, { useState } from "react";

import AddIcon from "@mui/icons-material/Add";
import DeleteForeverIcon from "@mui/icons-material/DeleteForever";
import TextField from "@mui/material/TextField";
import Box from "@mui/material/Box";
import IconButton from "@mui/material/IconButton";
import InputAdornment from "@mui/material/InputAdornment";

export const ListAddRemove = () =>{

const [items, setItems] = useState<string[]>([]);
const [value, setValue] = useState<string>("");

const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
  e.preventDefault();
  if (items.length < 10) {
    setItems([...items, value]);
    setValue("");
  }
};

const handleRemove = (index: number) => {
  const newItems = [...items];
  newItems.splice(index, 1);
  setItems(newItems);
};

return (
  <div>
    <form onSubmit={handleSubmit}>
      <TextField
        className={styles.input}
        value={value}
        onChange={(e) => setValue(e.target.value)}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
              <AddIcon sx={{ color: "#2A2D82", fontSize: 16 }} />
            </InputAdornment>
          ),
        }}
      />
    </form>

    <Box>
      {items.map((item, index) => (
        <div key={index} className={styles.listResult}>
          <div>{item}</div>
          <IconButton aria-label="delete" onClick={() => handleRemove(index)}>
            <DeleteForeverIcon sx={{ color: "#FF5151", fontSize: 25 }} />
          </IconButton>
        </div>
      ))}
    </Box>
  </div>
);

}


