
import styles from './CheckBox.module.scss';

import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import CheckOn from "../../../images/icons/checkOn.svg";
import CheckOff from "../../../images/icons/checkOff.svg";

import BookmarkBorderIcon from "@mui/icons-material/BookmarkBorder";

export const CheckBoxing = ({border, text}: {border?: any; text?: String}) => {

  const customStyle = {
    border: `${border}`,
  };  

  return (
    <div className={styles.border} style={customStyle}>
      <FormGroup className={styles.content}>
        <FormControlLabel
          control={
            <Checkbox
              //defaultChecked
              sx={{ "& .MuiSvgIcon-root": { fontSize: 28 } }}
              color="default"
              icon={<img src={CheckOff} />}
              checkedIcon={<img src={CheckOn} />}
            />
          }
          label={text}
        />
      </FormGroup>
    </div>
  );
}